import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';
import linkState from 'linkstate';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import Strava from '../../components/strava';
import Garmin from '../../components/garmin';
import Teams from '../../components/team/teams';

import FastList from '../../components/team/fastList';

const initialState = {
	isLoading: false,
	offset: 0,
	limit: 5,
	sessionid: new Date().getTime(),
};

const dataFields = ['id', 'type', 'user', 'team', 'title'];

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	// loadMore = () => {
	// 	const { offset } = this.state;
	// 	this.setState({ offset: offset + LIMIT }, () => {
	// 		this.loadWorkouts(true);
	// 	});
	// }

	loadWorkouts = async ({ limit, offset }) => {
		this.setState({ isLoading: true });
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		const append = offset > 0;
		await workoutStore.load({
			query: { team: currentTeam },
			limit,
			offset,
			addData: ['publicTeams', 'publicUsers'],
		}, append);
		this.setState({ isLoading: false });
	}

	changeTeam = () => {
		const { limit } = this.state;
        this.loadWorkouts({ limit, offset: 0 });
	}

    setLoadMore = () => {
        const { workoutStore } = this.props.stores;
        workoutStore.setLoadMore(() => {
            let { limit, offset } = this.state;
            offset += limit;
            this.setState({ offset });
            this.loadWorkouts({ limit, offset });
        });
    }

    unsetLoadMore = () => {
        const { workoutStore } = this.props.stores;
        workoutStore.setLoadMore(() => {});
    }

	loadDogs = async () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	}

	async loadTracks() {
		const { trackStore } = this.props.stores;
		await trackStore.load();
	}

	async loadWorkoutSummary() {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
        await userStore.getWorkoutSummary({ team: currentTeam });
    }

	async loadStravaActivities() {
		const { stravaActivityStore } = this.props.stores;
		stravaActivityStore.load({ query: { hideImported: 1 }});
	}

	async loadGarminActivities() {
		const { garminActivityDetailsStore } = this.props.stores;
		garminActivityDetailsStore.load({ query: { hideImported: 1 }});
	}

	removeFilterText = () => {
		this.setState({ filterText: '' });
	}

    renderContent = ({ obj, idx }) => {
        const { appState } = this.props.stores;
        const { path } = appState;
        const { hashtag } = this.props;

        return (<>
			<Workout
				stores={this.props.stores}
				workout={obj}
				showDate={true}
				wrapperClasses={'w-100'}
				wrapperClassesRow={''}
			/>
        </>);
    }

	// Scroll views timer
    setupIntersection = () => {
        this.setState({
            handleIntersection: mu.intersectionObserverCallback,
        });
        const { workoutStore } = this.props.stores;
        this.scrollViewTimer = setInterval(() => workoutStore.postScrollview(mu.getIntersectionObserverHistory()), 30 * 1000);
    }

	componentWillMount() {
        this.setupIntersection();
    }

	componentWillUnmount() {
        const { workoutStore, garminActivityDetailsStore, stravaActivityStore } = this.props.stores;
        workoutStore.postScrollview(mu.getIntersectionObserverHistory());
        clearInterval(this.scrollViewTimer);
        this.unsetLoadMore();
		// workoutStore.cleanupMemory();
    }
	// /Scroll views timer


    componentDidMount() {
		const { limit } = this.state;
        this.loadWorkouts({ limit, offset: 0 });
		this.loadDogs();
		// this.loadTracks();
		this.loadStravaActivities();
		this.loadGarminActivities();
		this.setLoadMore();
    }

	render() {
        const { scrolledDown } = this.props;
		const { isLoading, filterText, handleIntersection, sessionid } = this.state;
		const { trackStore, workoutStore, dogStore, appState, stravaActivityStore, garminActivityDetailsStore, userStore } = this.props.stores;
		const { darkmode } = appState;
		const { message } = this.props;
		const { workouts, currentTeam } = workoutStore;
		const currentTeamObject = userStore.findTeam(currentTeam, true);

		let workoutFilter = () => true;
		if (filterText) {
			workoutFilter = (e) => e.name.match(new RegExp(filterText, 'i'));
		}
		const workoutsFiltered = workouts.filter(workoutFilter);

		const { tracks } = trackStore;
		const { dogs } = dogStore;
		const { user } = userStore;
		const { stravaActivities = [] } = stravaActivityStore;
		const { garminActivityDetails = [] } = garminActivityDetailsStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top pt-2' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center subtopmenu'>
						<a href='/workouts/' class='text-secondary stretched-link'><nobr><i class='fas fa-chart-line' /> <Text id='workout.index.dashboard'>Oversikt</Text></nobr></a>
					</div>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<a href='/workouts/list' class='text-primary stretched-link'><nobr><i class='fas fa-list' /> <Text id='workout.index.list'>Liste</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/workouts/plan' class='text-secondary stretched-link'><nobr><i class='fas fa-calendar-alt' /> <Text id='workout.index.plan'>Plan</Text></nobr></a>
					</div>
				</div>

				{message && (
					<div class='alert alert-success mb-3' role='alert'>
						<i class='fas fa-check' /> {message}
					</div>
				)}

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.changeTeam} key={`teams-${currentTeam}`} />
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-2'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> <Text id='workout.index.add-workout'>Legg til treningstur</Text>
						</a>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3'>
						<div class='row mt-3'>
							{user && user.strava && <>
								<Strava stores={this.props.stores} activities={stravaActivities} key={`strava-${currentTeam}-${stravaActivities.length}`} />
							</>}
							{user && user.garmin && <>
								<Garmin stores={this.props.stores} activities={garminActivityDetails} key={`garmin-${currentTeam}-${garminActivityDetails.length}`} />
							</>}
						</div>
					</div>

					<div class='col-12'>

						{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-3'>

							<div class='input-group mb-3'>
								<Localizer>
	                                <input
										class='form-control form-control'
										type='text'
										autocomplete='off'
										value={filterText}
										placeholder={<Text id='workouts.search-for'>Sk etter trening</Text>}
										onInput={linkState(this, 'filterText')}
									/>
								</Localizer>
								{filterText && <div class='input-group-append'>
									<button class='btn btn-sm btn-outline-danger' type='button' onClick={this.removeFilterText}><i class='fas fa-times' /></button>
								</div>}
							</div>

                        </div>
 */}
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5>
								{/* <Text id='workouts.all-workouts' fields={{ total: workoutsFiltered.length }}>Treningsturene - {workoutsFiltered.length} stk</Text> */}
								{currentTeamObject && currentTeamObject.name && <div>
									<small class='font-weight-lighter'>(<Text id='workout.index.filtered-by'>Filtered by</Text>: {currentTeamObject.name})</small>
								</div>}
							</h5>
						</div>

						{!isLoading && workouts && workouts.length === 0 && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-muted'>
								{dogs && dogs.length === 0 && (
									<div class='mb-5 py-3 text-center border-top border-bottom'>
										<div class='display-4'>
											<i class='fas fa-dog' />
										</div>
										<h5>
											<Text id='workouts.no-dogs'>Ingen hunder er registert.</Text>
										</h5>
										<Text id='workouts.no-dogs-tip'>Det er lurt  legge inn hunder fr du registrerer en treningstur.</Text><br />
										<a class='btn btn-info mt-3' href='/dogs/edit/new'>
											<i class='fas fa-dog' /> <Text id='workouts.add-dog'>Legg til hund</Text>
										</a>
									</div>
								)}

								{tracks && tracks.length === 0 && (
									<div class='mb-5 py-3 text-center border-top border-bottom'>
										<div class='display-4'>
											<i class='fas fa-map-marked' />
										</div>
										<h5>
											<Text id='workouts.no-tracks'>Ingen runder er registert.</Text>
										</h5>
										<Text id='workouts.no-tracks-tip-1'>Det er lurt  legge inn runden fr du registrerer en treningstur. Da fr du automatisk fylt ut distanse og hydemeter.</Text><br />
										<Text id='workouts.no-tracks-tip-2'>Dersom du ikke har GPX-filen fra denne runden, s kan du likevel registrere treningen.</Text><br />

										<a class='btn btn-info mt-3' href='/tracks/edit/new'>
											<i class='fas fa-map-marked' /> <Text id='workouts.add-track'>Legg til runde</Text>
										</a>
									</div>
								)}

								<div class='text-center mb-5'>
									<div class='display-1'>
										<i class='fas fa-running' />
									</div>
									<h5>
										<Text id='workouts.no-workouts'>Ingen treningsturer er registrert enn.</Text>
									</h5>
								</div>
								<p class='text-center'>
									<p>
										<Text id='workouts.no-workouts-1'>Tracking i appen er ikke helt klart enn pga noen tekniske begrensninger i Apple iOS.</Text>
									</p>
									<p>
										<Text id='workouts.no-workouts-2'>Nr jeg fr tak i en Android telefon skal jeg fr satt opp denne funksjonen slik at den kan brukes der.</Text>
									</p>
								</p>
								<p class='text-center'>
									<Text id='workouts.no-workouts-3'>Kart over ruten du har kjrt legges inn som GPX under runder.</Text>
								</p>
								<Text id='workouts.no-workouts-howto'>Slik kommer du i gang</Text>:
								<ol>
									<li><Text id='workouts.no-workouts-howto-1'>Trykk p knappen "Legg til treningstur" oppe til hyre.</Text></li>
								</ol>
							</div>
						)}

						<FastList
							data={workoutsFiltered}
							wrapperClassNames={'row pb-4'}
							contentClassNames={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 my-1 position-relative rounded rounded-lg py-3 bg-${darkmode ? 'black' : 'white'} shadow-sm`}
							renderContent={this.renderContent}
							dataFields={dataFields}
							dataValues={{ type: '11' }}
							handleIntersection={handleIntersection}
							sessionid={sessionid}
						/>

						{/* {workoutsFiltered && workoutsFiltered.map(workout => (
							<Workout stores={this.props.stores} workout={workout} showDate={true} />
						))} */}
					</div>

					{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-center'>
						<button class='btn btn-sm btn-link text-muted'
							style='font-size: 1.5em;'
							onClick={this.loadMore}
						>
							<i class='fas fa-download' /> Load {LIMIT} more...
						</button>
					</div> */}

				</div>
			</div>
		);
	}
}

export default Workouts;
